html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  width: 100%;
  min-height: 100vh;
  font-family: "Montserrat", sans-serif;
}

a {
  display: block;
  text-decoration: none;
  color: black;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

@media (min-width: 1024px) {
  .contenedor {
    width: 80%;
  }
}
.checkout {
  margin-top: 3.2rem;
  margin-bottom: 2.4rem;
}
.checkout__title {
  font-size: 2.4rem;
  color: #4E5150;
}
@media (min-width: 768px) {
  .checkout__title {
    font-size: 3.6rem;
  }
}

.formulario {
  display: flex;
  justify-content: space-between;
}

.carrito {
  max-width: 38rem;
  padding: 2.6rem;
  background-color: #F2F2F2;
  border-radius: 1.2rem;
}
@media (min-width: 768px) {
  .carrito {
    order: 2;
  }
}
.carrito__resumen {
  padding-top: 6rem;
}
.carrito__montos {
  display: flex;
  justify-content: space-between;
  font-size: 1.4rem;
  flex-wrap: wrap;
}
.carrito__montos::before {
  content: "";
  display: block;
  width: 100%;
  height: 0.1rem;
  background-color: #BDBDBD;
}
@media (min-width: 768px) {
  .carrito__prop {
    font-size: 1.8rem;
  }
}

.producto {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.2rem;
  margin-bottom: 3.2rem;
}
.producto__detalles {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.producto__imagen {
  overflow: hidden;
  margin: 0;
  border-radius: 1.2rem;
}
.producto__nombre {
  margin: 0 0 1rem 0;
  font-size: 1.4rem;
}
.producto__precio {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.producto__oferta, .producto__monto {
  margin: 0;
}
.producto__oferta {
  font-size: 1.4rem;
  color: #F2994A;
}
.producto__monto {
  font-size: 1rem;
  color: #4E5150;
  text-decoration: line-through;
}
.producto__cantidad {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #828282;
  border-radius: 1.2rem;
  padding: 1.4rem 1rem;
}
.producto__mas, .producto__menos {
  background-color: #e2e2e2;
  border-radius: 0.5rem;
  transition: background-color 0.25s;
}
.producto__mas:active, .producto__menos:active {
  background-color: #c2c2c2;
}
.producto__number {
  margin: 0;
}